@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body,html,#root,.App{width:100%;height:100%;overflow:hidden;font-family:'Roboto', sans-serif;font-size:14px}.SiteContent{max-width:1440px;position:relative;height:100%;margin:0px auto}::-webkit-scrollbar{width:4px}::-webkit-scrollbar-track{-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.3);border-radius:2px;background-color:rgba(3,5,12,0.8)}::-webkit-scrollbar-thumb{border-radius:2px;-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.5);background-color:#b8860b}

@-webkit-keyframes Loading-Wrapper-Animation{0%{opacity:0.0}100%{opacity:1.0}}@keyframes Loading-Wrapper-Animation{0%{opacity:0.0}100%{opacity:1.0}}@-webkit-keyframes Loading-Wrapper-AnimationReversed{0%{opacity:1.0}100%{opacity:0.0}}@keyframes Loading-Wrapper-AnimationReversed{0%{opacity:1.0}100%{opacity:0.0}}.Loading-Wrapper{position:absolute;top:0px;bottom:0px;left:0px;right:0px;text-align:center;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;background-image:url(data:image/png;base64,)}.Loading-Wrapper:before{content:"";height:100%;width:0px;position:relative;display:inline-block;vertical-align:middle}.Loading-Wrapper .Loading-Container{display:inline-block;vertical-align:middle;perspective:100px}.Loading-Wrapper .Loading-Container img{width:50px;height:50px}.Loading-Wrapper .Loading-Container p{height:50px;font-weight:bold;color:darkgoldenrod;-webkit-filter:drop-shadow(0px 0px 5px darkgoldenrod);filter:drop-shadow(0px 0px 5px darkgoldenrod);transform:rotateX(40deg)}.Loading-Wrapper.Loading-Wrapper-FadeInAnimation{-webkit-animation:Loading-Wrapper-Animation 200ms ease-out;animation:Loading-Wrapper-Animation 200ms ease-out}.Loading-Wrapper.Loading-Wrapper-FadeOutAnimation{-webkit-animation:Loading-Wrapper-AnimationReversed 200ms ease-out;animation:Loading-Wrapper-AnimationReversed 200ms ease-out;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}

@-webkit-keyframes AnimatedBackground-NearPlane-Animation{0%{transform:translateY(100%)}100%{transform:translateY(0%)}}@keyframes AnimatedBackground-NearPlane-Animation{0%{transform:translateY(100%)}100%{transform:translateY(0%)}}@-webkit-keyframes AnimatedBackground-FarPlane-Animation{0%{transform:translateY(10%);-webkit-filter:brightness(100%);filter:brightness(100%)}100%{transform:translateY(0%);-webkit-filter:brightness(50%);filter:brightness(50%)}}@keyframes AnimatedBackground-FarPlane-Animation{0%{transform:translateY(10%);-webkit-filter:brightness(100%);filter:brightness(100%)}100%{transform:translateY(0%);-webkit-filter:brightness(50%);filter:brightness(50%)}}.AnimatedBackground-Wrapper{position:absolute;top:0px;bottom:0px;left:0px;right:0px;text-align:center;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;overflow:hidden}.AnimatedBackground-Wrapper>*{position:absolute}.AnimatedBackground-Wrapper .AnimatedBackground-Logo{position:absolute;left:5px;top:5px;padding:10px;height:30px;border-radius:10px;border-bottom:1px solid darkgoldenrod;border-right:1px solid darkgoldenrod;background-color:rgba(62,69,84,0.4);background-color:rgba(0,0,0,0.9)}.AnimatedBackground-Wrapper .AnimatedBackground-FarPlane{left:0px;bottom:0px;min-height:200%;min-width:100%;background-image:url(/swapi-UI/static/media/stars.511c5034.jpg);transform:translateY(50%);-webkit-filter:brightness(100%);filter:brightness(100%);will-change:transform}.AnimatedBackground-Wrapper .AnimatedBackground-NearPlane{left:0px;bottom:0px;max-height:100%;max-width:900px;transform:translateY(100%);will-change:transform}.AnimatedBackground-Wrapper-Animate .AnimatedBackground-FarPlane{-webkit-animation:AnimatedBackground-FarPlane-Animation 3s cubic-bezier(0.25, 0, 0, 0.9);animation:AnimatedBackground-FarPlane-Animation 3s cubic-bezier(0.25, 0, 0, 0.9);-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}.AnimatedBackground-Wrapper-Animate .AnimatedBackground-NearPlane{-webkit-animation:AnimatedBackground-NearPlane-Animation 3s cubic-bezier(0.25, 0, 0, 0.9);animation:AnimatedBackground-NearPlane-Animation 3s cubic-bezier(0.25, 0, 0, 0.9);-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}

.TabWindow{padding:0px;margin:5px;width:calc(100% - 10px);height:calc(100% - 10px);color:#fff;box-sizing:border-box}.TabWindow .TabWindow-Buttons{padding:0px;margin:0px;height:30px;width:100%;text-align:right;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.TabWindow .TabWindow-Buttons li{padding:0px;margin:0px;display:inline-block;height:30px}.TabWindow .TabWindow-Buttons li a{height:25px;margin:0px 0px 0px 5px;line-height:25px;color:#b8860b;font-weight:500;cursor:pointer;display:block;padding-left:10px;padding-right:10px;border-right:1px solid #b8860b;transition:padding 100ms ease, border 100ms ease, color 100ms ease;letter-spacing:1px;font-size:1.1rem;text-shadow:0px 0px 2px rgba(255,165,0,0.5)}.TabWindow .TabWindow-Buttons li a:hover{padding-right:8px;border-right:3px solid #b8860b;color:#ffe81f}.TabWindow .TabWindow-Container{position:relative;border:1px solid #b8860b;height:calc(100% - 30px);width:100%;box-shadow:0px 0px 2px 0px rgba(0,0,0,0.7);padding:5px;box-sizing:border-box;overflow-y:auto;background-image:url(data:image/png;base64,);font-weight:300;font-size:1rem}.TabWindow .TabWindow-Container.TabWindow-Container-NoButtons{height:100%}

.SlidingContainer{position:absolute;top:0px;bottom:0px;opacity:0}.SlidingContainer.SlidingContainer-FromLeft{left:0px;transform:translateX(-100%);transition:opacity 1s cubic-bezier(0, 0, 0, 1),transform 1s cubic-bezier(0, 0, 0, 1)}.SlidingContainer.SlidingContainer-FromLeft.SlidingContainer-FromLeft-Opened{opacity:1;transform:translateX(0%)}.SlidingContainer.SlidingContainer-FromRight{right:0px;transform:translateX(50%);transition:opacity 1s cubic-bezier(0, 0, 0, 1),transform 1s cubic-bezier(0, 0, 0, 1);transition-delay:200ms}.SlidingContainer.SlidingContainer-FromRight.SlidingContainer-FromRight-Opened{opacity:1;transform:translateX(0%)}

.EntriesMenu-Wrapper{display:block}.EntriesMenu-Wrapper .EntriesMenu{padding:0px 5px;margin:0px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;overflow-y:auto;height:calc(100% - 40px)}.EntriesMenu-Wrapper .EntriesMenu li{display:block;height:30px;line-height:30px;border:1px solid transparent;cursor:pointer;padding:5px}.EntriesMenu-Wrapper .EntriesMenu li:hover{background-color:rgba(184,134,11,0.5);border:1px solid #ffe81f}.EntriesMenu-Wrapper .EntriesMenu-Pagination{height:40px;padding:0px;margin:0px;width:100%;height:40px;line-height:40px;text-align:center;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.EntriesMenu-Wrapper .EntriesMenu-Pagination li{display:inline-block;margin:0px;padding:0px 5px;color:#b8860b;font-weight:500;cursor:pointer;font-size:1.1rem}.EntriesMenu-Wrapper .EntriesMenu-Pagination li:hover{color:#ffe81f}.EntriesMenu-Wrapper .EntriesMenu-Pagination li.EntriesMenu-Pagination-Current{color:#ffe81f;cursor:default;text-shadow:0px 0px 3px orange}.EntriesMenu-Wrapper .EntriesMenu-Image{max-height:30px;max-width:30px;width:100%;margin:0px 10px 0px 0px}

.PlaceholderImage{border:1px solid #b8860b;background-color:rgba(184,134,11,0.5);vertical-align:middle}

.EntryEmpty{display:block;width:100%;height:100%;padding:0px;margin:0px;text-align:center}.EntryEmpty p{display:inline-block;vertical-align:middle;padding:0px;margin:0px;color:#b8860b;text-shadow:0px 0px 2px orange;font-size:1.5rem}.EntryEmpty p img{margin:0px 0px 20px 0px;opacity:0.5}.EntryEmpty:before{content:"";display:inline-block;vertical-align:middle;width:0px;height:100%}


.EntryDataTable-Container{display:block;border-top:1px solid #b8860b;margin:10px 0px;border-left:1px solid #b8860b;border-right:1px solid #b8860b}.EntryDataTable-Container .EntryDataTable-Container-Title{display:block;margin:0px;padding:10px 5px 10px 30px;border-bottom:1px solid #b8860b;font-size:1.5rem;background-color:rgba(184,134,11,0.2);color:#ffe81f;text-shadow:0px 0px 2px orange;font-weight:300;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.EntryDataTable-Container .EntryDataTable-Container-Row{display:flex;position:relative;border-bottom:1px solid #b8860b}.EntryDataTable-Container .EntryDataTable-Container-Row .EntryDataTable-Container-Row-Image{display:inline-block;vertical-align:top;max-height:200px;max-width:200px;flex:1 1}.EntryDataTable-Container .EntryDataTable-Container-Row .EntryDataTable-Container-Row-Image img{max-height:200px;max-width:200px;box-sizing:border-box;border-top:none;border-bottom:none;border-left:none}.EntryDataTable-Container .EntryDataTable-Container-Row .EntryDataTable-Container-Row-Informations{padding:5px 10px;margin:0px;flex:1 1;display:inline-block;vertical-align:top;font-size:0.9rem}.EntryDataTable-Container .EntryDataTable-Container-Row .EntryDataTable-Container-Row-Informations li{display:block}.EntryDataTable-Container .EntryDataTable-Container-Row.EntryDataTable-Container-Row-Clickable{cursor:pointer}.EntryDataTable-Container .EntryDataTable-Container-Row.EntryDataTable-Container-Row-Clickable:hover{background-color:rgba(184,134,11,0.3)}.EntryDataTable-Container .EntryDataTable-Container-Row.EntryDataTable-Container-Row-Clickable:hover:after{max-width:40px}.EntryDataTable-Container .EntryDataTable-Container-Row.EntryDataTable-Container-Row-Clickable:after{content:"";text-align:center;flex:1 1;max-width:20px;background-color:#304b67;transition:max-width 100ms ease-out;background-image:url(data:image/png;base64,);background-size:20px;background-repeat:no-repeat;background-position:50% 50%;background-color:rgba(184,134,11,0.7);border-left:1px solid #b8860b}.EntryDataTable-Container.EntryDataTable-Container-Small .EntryDataTable-Container-Title{padding:5px 5px 5px 10px;font-size:1rem}.EntryDataTable-Container.EntryDataTable-Container-Small .EntryDataTable-Container-Row .EntryDataTable-Container-Row-Image{max-height:50px;max-width:50px}.EntryDataTable-Container.EntryDataTable-Container-Small .EntryDataTable-Container-Row .EntryDataTable-Container-Row-Image img{max-height:50px;max-width:50px}.EntryDataTable-Container.EntryDataTable-Container-Small .EntryDataTable-Container-Row .EntryDataTable-Container-Row-Image ~ .EntryDataTable-Container-Row-Informations{line-height:50px;padding-top:0px;padding-bottom:0px}





.Quotes{padding:0px 20px 0px 20px;margin:0px}.Quotes .Quotes-Quote{display:block;height:calc(100% - 20px);padding:5px 0px 0px 0px;margin:0px;font-style:italic;color:#b8860b;text-align:center;text-shadow:0px 0px 2px orange}.Quotes .Quotes-Quote:before{content:"\201E"}.Quotes .Quotes-Quote:after{content:"\201D"}.Quotes .Quotes-Author{display:block;height:15px;padding:0px;margin:0px;text-align:right;line-height:15px;font-size:12px;color:#b8860b;text-shadow:0px 0px 2px orange}

